import { ElementRef, EventEmitter, Injectable, Renderer2, TemplateRef } from '@angular/core';
/* RXJS */
import { delay, of } from 'rxjs';

class ListTabsModel {
  position: number;
  title: string | TemplateRef<any> | any;

  constructor(position: number, title: string | TemplateRef<any>) {
    this.position = position;
    this.title = title
  }
}

@Injectable()
export class GdaTabsPrivateService {
  private gdaTabsEl!: HTMLElement;
  callForId: EventEmitter<number>;
  private listTabs: ListTabsModel[];
  buttonLoaded: EventEmitter<ListTabsModel[]>;
  private indexTabVal: number;
  indexTabChanges: EventEmitter<{new: number, old: number}>;
  indexTabChangesEmit: EventEmitter<number>;
  private animation!: boolean;

  constructor() {
    this.callForId = new EventEmitter();
    this.listTabs = [];
    this.buttonLoaded = new EventEmitter();
    this.indexTabVal = 0;
    this.indexTabChanges = new EventEmitter();
    this.indexTabChangesEmit = new EventEmitter();
  }

  public tabLoaded(el: HTMLElement, i?: number): void {
    if (!this.gdaTabsEl) this.gdaTabsEl = el;
    if (i === undefined) {
      setTimeout(() => this.createTabs());
    } else {
      const tabsElN = this.gdaTabsEl.querySelectorAll('gda-tab')?.length || 0;
      if (tabsElN === (i + 1)) this.createTabs();
    }
  }

  private createTabs(): void {
    this.listTabs = [];
    const tabs = this.gdaTabsEl.querySelectorAll('gda-tab');
    tabs.forEach((tab, index: number) => this.callForId.emit(index));
    this.buttonLoaded.emit(this.listTabs);
  }

  public addTab(tab: ListTabsModel): void {
    this.listTabs.push(tab);
  }

  public getIndexTab(): number {
    return this.indexTabVal;
  }

  public setIndexTab(indexTab: number): void {
    this.indexTabChanges.emit({new: indexTab, old: this.indexTabVal});
    this.indexTabVal = indexTab;
  }

  public setTitle(position: number, title: string): void {
    if (this.listTabs.some((tab: ListTabsModel) => tab.position = position)) {
      this.listTabs[position].title = title;
      this.buttonLoaded.emit(this.listTabs);
    }
  }

  public getAnimation(): boolean | undefined {
    return this.animation;
  }

  public setAnimation(animation: boolean): void {
    this.animation = animation;
  }
}
