import { AfterViewInit, ChangeDetectionStrategy, ChangeDetectorRef, Component, ElementRef, HostBinding, Input, OnDestroy, Renderer2, TemplateRef, ViewChild } from '@angular/core';
/* SERVICE */
import { GdaTabsService } from './gda-tabs.service';
import { GdaTabsPrivateService } from './gda-tabs-private.service';
/* RXJS */
import { delay, of, Subscription } from 'rxjs';
/* ANIMATION */
import { trigger, transition, style, animate, keyframes } from '@angular/animations';

@Component({
  changeDetection: ChangeDetectionStrategy.OnPush,
  selector: 'gda-tab',
  template: `
    <div 
      class="gda-tabs-tab-content"
      *ngIf="onTabActive()"
      [@tabsAnimation]="{value: '*', params: { leftEnter: leftEnter, leftLeave: leftLeave, animations: animations ? '0.5s ease-in-out' : '0s'}}"
      #contentEl>
      <ng-content></ng-content>
    </div>
  `,
  animations: [
    trigger('tabsAnimation', [
      transition(':enter', [
        style({ position: 'absolute', left: '{{ leftEnter }}' }),
        animate('{{ animations }}',
          keyframes([
            style({
              left: '{{ leftEnter }}'
            }),
            style({
              left: 0
            })
          ])
        )
      ]),
      transition(':leave', [
        style({ position: 'absolute', left: 0 }),
        animate('{{ animations }}',
          keyframes([
            style({
              left: 0
            }),
            style({
              left: '{{ leftLeave }}'
            })
          ])
        )
      ])
    ])
  ]
})
export class GdaTabComponent implements AfterViewInit, OnDestroy {
  /* CLASS */
  @HostBinding('class') setClass = 'gda-tab';
  @HostBinding('style.height') heightTab = '0';
  @ViewChild('contentEl', { static: false }) contentEl!: ElementRef;
  /* TITLE */
  @Input() set titleTab(title: string | TemplateRef<any> | any) {
    this.tabTitleVal = title;
    if (this.position !== null) {
      this.gdaTabsPrivateService.setTitle(this.position, title);
    }
  };
  private tabTitleVal: string | TemplateRef<any> | any;
  private position: number | null;
  public leftEnter: string;
  public leftLeave: string;
  public animations: boolean;
  private sub1: Subscription;
  private sub2: Subscription;
  private sub3: Subscription;

  constructor(
    private cd: ChangeDetectorRef,
    private elementRef: ElementRef,
    private renderer: Renderer2,
    private gdaTabsService: GdaTabsService,
    private gdaTabsPrivateService: GdaTabsPrivateService
  ) {
    this.tabTitleVal = '';
    this.position = null;
    this.leftEnter = '0';
    this.leftLeave = '0';
    this.animations = false;
    this.sub1 = this.gdaTabsPrivateService.callForId.subscribe((position: number) => {
      const container = this.renderer.parentNode(this.elementRef.nativeElement);
      container.querySelectorAll('gda-tab').forEach((tab: HTMLElement, index: number) => {
        if (tab === this.elementRef.nativeElement) this.position = index;
      });
      if (position === this.position) this.gdaTabsPrivateService.addTab({ position: position, title: this.tabTitleVal });
      if (this.contentEl) this.heightTab = this.contentEl.nativeElement.offsetHeight + 'px';
      this.animations = false;
      this.cd.detectChanges();
    });
    this.sub2 = this.gdaTabsPrivateService.indexTabChanges.subscribe((change: { new: number, old: number }) => {
      if (change.new === this.position) {
        if (this.position !== null) {
          if (this.position > change.old) {
            this.leftEnter = '100%';
          } else if (this.position < change.old) {
            this.leftEnter = '-100%';
          }
        }
        this.cd.detectChanges();
        setTimeout(() => {
          this.cd.detectChanges();
          this.heightTab = this.contentEl.nativeElement.offsetHeight + 'px';
        });
      } else {
        if (this.position !== null) {
          if (this.position > change.new) {
            this.leftLeave = '100%';
          } else if (this.position < change.new) {
            this.leftLeave = '-100%';
          }
        }
        this.cd.detectChanges();
        setTimeout(() => {
          this.cd.detectChanges();
          // this.heightTab = 'min-content';
        });
      }
    });
    this.sub3 = this.gdaTabsPrivateService.buttonLoaded.subscribe(() => of(true).pipe(delay(100)).subscribe(() => {
      const animationPrivate: boolean | undefined = this.gdaTabsPrivateService.getAnimation();
      if (animationPrivate !== undefined) this.animations = animationPrivate
      else if (this.gdaTabsService.animationsActivated !== undefined) this.animations = this.gdaTabsService.animationsActivated
      else this.animations = true;
    }));
  }

  ngAfterViewInit(): void {
    setTimeout(() => {
      this.gdaTabsPrivateService.tabLoaded(this.renderer.parentNode(this.elementRef.nativeElement));
      this.cd.detectChanges();
    });
  }

  public onTabActive(): boolean {
    return this.position === this.gdaTabsPrivateService.getIndexTab();
  }

  ngOnDestroy(): void {
    this.sub1.unsubscribe();
    this.sub2.unsubscribe();
    this.sub3.unsubscribe();
    this.gdaTabsPrivateService.tabLoaded(this.renderer.parentNode(this.elementRef.nativeElement));
  }



}
